<template>
  <div id="content">

    <div id="sidebar">
      <v-app>
        <div class="filter-wrapper">
          <div class="filter-group">
            <v-select
              :items="types"
              v-model="typesPicked"
              item-text="name"
              item-value="id"
              label="Что ищешь?"
              attach
              chips
              deletable-chips
              dense
              multiple
            ></v-select>
          </div>
          <div class="filter-group">
            <v-select
              :items="tags"
              v-model="tagsPicked"
              item-text="name"
              item-value="id"
              label="Уточнить?"
              attach
              chips
              deletable-chips
              dense
              multiple
            ></v-select>
          </div>
          <div class="filter-group">
            <v-select
              :items="rates"
              v-model="ratesPicked"
              item-text="name"
              item-value="rate"
              label="Тонкая настройка"
              attach
              chips
              deletable-chips
              dense
              multiple
            ></v-select>
          </div>
          <div class="filter-group">
            <v-btn @click="applyFilters" class="ml-3" outlined color="teal darken-2">
              <v-icon left>mdi-filter-variant</v-icon> Применить
            </v-btn>
          </div>
        </div>
      </v-app>
    </div>

    <v-container class="content-wrapper">
      <div id="burger"
          :class="{ active: isMenuVisible }"
          @click.prevent="toggle">
        <button type="button" class="burger-button" title="Menu">
          <span class="burger-bar burger-bar--1"></span>
          <span class="burger-bar burger-bar--2"></span>
          <span class="burger-bar burger-bar--3"></span>
        </button>
      </div>
      <v-layout row wrap>
        <v-flex xs12 md12 lg6 xl4 v-for="item in info" :key="item.id">
          <card :info="item"></card>
        </v-flex>
      </v-layout>
    </v-container>

    <infinite-loading :identifier="infiniteId" @infinite="infiniteHandler" spinner="spiral">
      <div slot="no-more"></div>
      <div slot="no-results"></div>
    </infinite-loading>

  </div>
</template>

<script>
  import axios from 'axios'
  import Card from './Card'
  import Vuetify from 'vuetify'

  const urlCards = '/api/v1/cards/'
  const urlFilters = '/api/v1/filters/'

  export default {
    name: 'Content',
    vuetify: new Vuetify(),
    data () {
      return {
        info: [],
        next_page: urlCards + '?page=1',
        errored: false,
        isMenuVisible: true,
        typesPicked: null,
        types: [],
        tagsPicked: null,
        tags: [],
        ratesPicked: null,
        rates: [],
        infiniteId: +new Date(),
        params: {}
      }
    },
    methods: {
      applyFilters () {
        this.info = []
        this.params = {
          type: this.typesPicked + '',
          tags: this.tagsPicked + '',
          rating: this.ratesPicked + ''
        }
        this.next_page = urlCards + '?page=1'
        this.infiniteId += 1
      },
      infiniteHandler ($state) {
        axios.get(this.next_page, {
          params: this.params
        })
          .then(response => {
            this.info = this.info.concat(response.data.results)
            this.next_page = response.data.next
            if (this.next_page == null) {
              $state.complete()
            } else {
              $state.loaded()
            }
          })
          .catch(error => {
            console.log(error)
            this.errored = true
          })
      },
      toggle () {
        this.isMenuVisible = !this.isMenuVisible
      }
    },
    components: { Card },
    created () {
      axios.get(urlFilters)
        .then(response => {
          let data = response.data
          this.types = data.types
          this.tags = data.tags
          this.rates = data.rates
        })
        .catch(error => {
          console.log(error)
          this.errored = true
        })
    }
  }
</script>

<style scoped>
  @import url('https://fonts.googleapis.com/css?family=Montserrat:300,400&display=swap');

  .content-wrapper {
    margin-left: 21rem;
  }

  @media screen and (max-width: 600px) {
    .content-wrapper {
      margin-left: 0;
    }
  }

  /* Sidebar */
  #sidebar {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 21rem;
    z-index: 333;
    overflow: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
    background: #f8f8f8d7;
    box-shadow: inset -1px 0px #00000030;
  }

  #sidebar::-webkit-scrollbar {
    display: none;
  }

  .filter-wrapper {
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

  .filter-group {
    margin-bottom: 1rem;
  }

  .filter-name {
    text-align: left;
    margin-left: 1rem;
    color: #00000070;
  }

  .v-select {
    margin-left: 7%;
    width: 85%;
  }

  /* Menu toogle */
  .burger-button:focus {
    outline: 0;
  }

  .burger-button {
    cursor: pointer;
    position: relative;
    height: 30px;
    width: 32px;
    display: block;
    z-index: 999;
    border: 0;
    border-radius: 0;
    background-color: transparent;
    pointer-events: all;
    transition: transform .6s cubic-bezier(.165,.84,.44,1);
  }

  .burger-bar {
    background-color: #00000090;
    position: absolute;
    top: 50%;
    right: 6px;
    left: 6px;
    height: 2px;
    width: auto;
    margin-top: -1px;
    transition: transform .6s cubic-bezier(.165,.84,.44,1),
      opacity .3s cubic-bezier(.165,.84,.44,1),
      background-color .6s cubic-bezier(.165,.84,.44,1);
  }

  .burger-bar--1 {
    -webkit-transform: translateY(-6px);
    transform: translateY(-6px);
  }

  .burger-bar--2 {
    transform-origin: 100% 50%;
    transform: scaleX(.8);
  }

  .burger-button:hover .burger-bar--2 {
    transform: scaleX(1);
  }

  .no-touchevents .burger-bar--2:hover {
    transform: scaleX(1);
  }

  .burger-bar--3 {
    transform: translateY(6px);
  }

  #burger.active .burger-button {
    transform: rotate(-180deg);
  }

  #burger.active .burger-bar--1 {
    transform: rotate(45deg)
  }

  #burger.active .burger-bar--2 {
    opacity: 0;
  }

  #burger.active .burger-bar--3 {
    transform: rotate(-45deg)
  }

  #burger {
    display: none;
  }

  @media screen and (max-width: 600px) {
    #burger {
      display: block;
    }
  }
</style>



