<template>
  <div class="card">
    <v-card
      tile
      outlined
      :color="info.color + '10'">
      <v-layout row wrap>
        <v-flex xs12 sm5 class="image-wrapper">
          <v-img
            :src="info.imgur_url"
            aspect-ratio="1"
            height="17rem"
            contain
          >
            <template v-slot:placeholder>
              <div class="load-img-wrapper">
                <v-progress-circular
                  indeterminate
                  :color="info.color + ' lighten-5'"
                  style="margin: auto"></v-progress-circular>
              </div>
            </template>
          </v-img>
        </v-flex>
        <v-flex xs12 sm7>
          <div class="text-wrapper">
            <span :type="info.type.id" class="type">{{info.type.name.toLowerCase()}}</span>
            <h1>{{info.title}}</h1>
            <p :rating="info.rating.rate" class="rating">{{info.rating.name}}</p>
            <div class="tags-wrapper">
              <div class="tag" v-for="item in info.tags" :key="item.id">
                {{item.name}}
              </div>
            </div>
          </div>
        </v-flex>
      </v-layout>
    </v-card>
  </div>
</template>

<script>
  export default {
    name: 'Card',
    props: ['info'],
    data () {
      return {

      }
    }
  }
</script>

<style scoped>
  @import url('https://fonts.googleapis.com/css?family=Oswald:200,300&display=swap');


  .card {
    font-family: 'Montserrat', sans-serif;
  }

  div.card .v-card {
    overflow: hidden;
    border: none;
  }

  .image-wrapper {
    margin: 1rem 0;
  }

  .load-img-wrapper {
    display: flex;
    justify-content: center;
    height: 17rem;
    width: 100%;
  }

  .text-wrapper {
    width: 95%;
    margin-left: 0.3rem;
    margin-right: 5%;
    margin-top: 0.7rem;
    text-align: left;
    color: #000000;
  }

  h1 {
    font-family: 'Oswald', sans-serif;
    font-weight: 300;
    margin-top: 0
  }

  span, p {
    font-weight: 400;
  }

  span.type {
    color: #a7a7a7
  }

  p.rating {
    margin-top: 1.3rem;
  }

  .tags-wrapper {
    margin-top: 5.1rem;
  }

  .tag {
    font-size: 0.9rem;
    display: inline-block;
    padding: 0.1rem 0.5rem;
    border-radius: 0.3rem;
    background: #e2e2e2;
    margin-right: 0.3rem;
    margin-bottom: 0.3rem;
    opacity: 0.5;
  }

  @media screen and (max-width: 600px) {
    .text-wrapper {
      margin-bottom: 3rem;
      text-align: center;
      margin-left: 0;
    }
  }
</style>



